import React from 'react';
import {AppBar, Box, Toolbar, Typography} from '@mui/material';
import {appbarStyle, nameStyle, styleButtons, toolbarStyle} from "./stylesheets/navbarStyles";
import './stylesheets/styles.css';
const Navbar = () => {
    const scrollToSection = (id) => {
        const element = document.getElementById(id);
        if (element) {
            element.scrollIntoView({ behavior: 'smooth' });
        }
    };

    const data = [
        {
            title: "Home",
            data: "home",
        },

        {
            title: "Services",
            data: "services",
        },

        {
            title: "Solutions",
            data: "solutions",
        },

        {
            title: "Team",
            data: "team",
        },

        {
            title: "About",
            data: "about",
        },
    ]

    return (
        <div>
            <AppBar position="static" sx={appbarStyle}>
                <Toolbar sx={toolbarStyle}>
                    <Typography variant="h6" sx={nameStyle}>
                        Syed Saqlain Security Services LLC
                    </Typography>
                    <Box>
                        {data.map((Item) =>(<span className={"button"} onClick={() => scrollToSection(Item.data)}>
                            {Item.title}
                        </span>))}
                    </Box>
                </Toolbar>
            </AppBar>
        </div>
    );
};

export default Navbar;
