import {Box, Typography, useMediaQuery, useTheme} from "@mui/material";
import Header from "./client/Header";
import Services from "./client/Services";
import Video from "./client/Video";
import Mission from "./client/Mission";
import About from "./client/About";
import Vision from "./client/Vision";
import Solutions from "./client/Solutions";
import ServicesCard from "./client/servicesCard";
import Team from "./client/Team";
import Testimonials from "./client/Testimonials";
import Footer from "./client/Footer";
import Navbar from "./client/Navbar";
import React from "react";
import {createRoot} from "react-dom/client";
import './index.css';

const Index = () => {
  const theme = useTheme();
  const isSmallScreen = useMediaQuery(theme.breakpoints.down('md'));

  return (!isSmallScreen ? (<Box>
            <Navbar></Navbar>
            <Header></Header>
            <About></About>
            <Mission></Mission>
            <Vision></Vision>
            <Services></Services>
            <Video></Video>
              <ServicesCard></ServicesCard>
              <Solutions></Solutions>
              <Team></Team>
              <Testimonials></Testimonials>
          <Footer></Footer>
          </Box>) :
          (<Box sx={{alignItems: 'center', justifyContent: 'center', display: 'flex'}}>
              <Typography variant={'h1'}>Mobile Screen View Coming Soon</Typography>
          </Box>)

  );
}

createRoot(document.getElementById('root')).render(<Index />);
